import tbRadio from "./tbRadio/index"; // tb-radio
import tbRadioButton from "./tbRadioButton/index"; // tb-radio-button
import tbRadioGroup from "./tbRadioGroup/index"; // tb-radio-group

import tbCheckbox from "./tbCheckbox/index"; // tb-checkbox
import tbCheckboxButton from "./tbCheckboxButton/index"; // tb-checkbox-button
import tbCheckboxGroup from "./tbCheckboxGroup/index"; // tb-checkbox-group

import tbTag from "./tbTag/index"; // tag

import tbColor from "./tbColor/index"; // 
import tbColorPicker from "./tbColorPicker/index"; // 

import tbCalendar from "./tbCalendar/index"; // 

import tbPopconfirm from "./tbPopconfirm/index"; // 

import tbDivider from "./tbDivider/index"; // 

import tbBadge from "./tbBadge/index"; // 

import tbAvatar from "./tbAvatar/index"; // 

